.header{
    height:70px;
    width:100%;
    position:fixed;
    top:0px;
    left:0px;
    background: #0f0c29;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #24243e, #302b63, #0f0c29);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    z-index: 12;
}
.logo{
    position:absolute;
    left:50%;
    top:5px;
    transform:translateX(-50%);
}
.mobileMenu{
    position: absolute;
    right:20px;
    top:14px;
    padding:3px;
    border: 0px solid white;
}
.mobileNotifications{
    position: absolute;
    left:20px;
    top:18px;
    padding:3px;
    border: 0px solid white;
}
.mobileNotificationsIcon{
    font-size: 2.1em;
    color:white;
}
.mobileMenuBarIcon{
    font-size: 2.4em;
    color:white !important;
}
.menuItems{
    color:white;
    position:absolute;
    top:24px;
    right:40px;
    display:none;
    list-style: none;
    margin:0px;
}
.menuItems > li{
    display: inline-block;
    font-size:18px;
    margin:0px 10px 0px 10px;
    font-weight: 700;
    font-family: 'Maven Pro', sans-serif;
}
.mobileMenuContainer{
    width:300px;
    height:calc(100vh - 70px);
    position:fixed;
    top:70px;
    right:0px;
    background-color:white;
    border-left:2px solid gray;
    display:none;
}
.mobileMenuContainer{
    width:300px;
    height:calc(100vh - 70px);
    position:fixed;
    top:70px;
    right:0px;
    background-color:white;
    border-left:2px solid gray;
    display:none;
}
.mobileNotificationContainer{
    width:100vw;
    height:calc(100vh - 70px);
    position:fixed;
    top:70px;
    right:0px;
    background-color:white;
    border-left:2px solid gray;
    display:none;
}
@media only screen and (min-width: 910px) {
    .mobileMenu{
        display: none;
    }
    .logo{
        left:10%;
    }
    .mobileNotifications{
        display: none;
    }
    .menuItems{
        display: block;
    }
    .mobileMenuContainer{
        display: none;
    }
}